* {
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

.headlineCon{
    display: flex;
    flex-direction: column;
    align-items: center;
   font-family: 'Karla', sans-serif;
   font-weight: 500;
   padding-top: 6vw;
   font-size: 3vw;

}

.container {
    width: 100%;
    height: 40vh;
    /* background-color: rgb(233, 233, 240); */
    position: relative;
    overflow: hidden;
}

.bub {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    backdrop-filter: blur(2px);
    position: absolute;
    bottom: -100px;
    animation: bubble 5s ease-in-out infinite;
}
.bub img{
    border-radius: 50%;
}
.bub::before {
    position: absolute;
    content: "";
    width: 20px;
    height: 20px;
    border-radius: 50%;
    top: 25px;
    right: 23px;
}

.bub.a {
    left: 10%;
    animation: bubble2 2s ease-in 1s infinite;
}

.bub.b {
    left: 20%;
    animation: bubble 1.5s ease-in 1.4s infinite;
}

.bub.c {
    left: 28%;
    animation: bubble 5s ease-in 3.8s infinite;
}

.bub.d {
    left: 40%;
    animation: bubble 1.8s ease-in .5s infinite;
}

.bub.e {
    left: 75%;
    animation: bubble2 2.3s ease-in 2.5s infinite;
}

.bub.f {
    left: 80%;
    animation: bubble 2.5s ease-in 3s infinite;
}

.bub.g {
    left: 60%;
    animation: bubble 2.2s ease-in 2s infinite;
}

.bub.k {
    left: 50%;
    animation: bubble 2s ease-in 2s infinite;
}

.bub.i {
    left: 65%;
    animation: bubble2 1.8s ease-in 2.1s infinite;
}

.bub.j {
    left: 3%;
    animation: bubble 2s ease-in 1.5s infinite;
}

.bub.h {
    left: 40%;
    animation: bubble2 3s ease-in infinite;
}

@keyframes bubble {
    0% {
        opacity: 0;
    }
    10%,
    93% {
        opacity: 1;
    }
    100% {
        transform: translate(90px, -700px);
        display: none;
    }
}

@keyframes bubble2 {
    0% {
        opacity: 0;
    }
    10%,
    93% {
        opacity: 1;
    }
    100% {
        transform: translate(-90px, -700px);
        display: none;
    }
}